svg text {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.annotationDescription {
  pointer-events: none;
  font-size: 14px;
  position: absolute;
  top: 20px;
  padding: 1em;
  width: 200px;
  text-color: rgba(17, 24, 39,  0.66);
  border-radius: 0.5em;
  display: block;
}

#annotationsPanel   {
  position: absolute;
  top: 120px;
  left: 4px;
  width: auto;
  height: auto;
}


#apartmentsPanel   {
  position: absolute;
  top: 120px;
  right: 4px;
  width: auto;
  height: auto;
}


.annotationButton {
  width: 160px;
  height: 70px;
  background-color: rgba(17, 24, 39,  0.66);
  text-align: left;
  padding-left: 20px;
  cursor: pointer;
  color: rgb(209, 213, 219);
  font-family: monospace;
}


div.label {
  background-color: white;
  border: 1px solid #888888;
  padding: 5px;
  border-radius: 3px;
  font-family: Arial, Helvetica, sans-serif;
}

annotation {

  margin-bottom: 100;
}



.annotation::before {
  content: '';
  position: absolute;
  top: 50px;
  right: 120px;
  width: 2px;
  height: 30px;
  background: #FFFFFF;
}
